@import url(https://fonts.googleapis.com/css?family=Ubuntu:700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Gochi+Hand|Staatliches&display=swap);
@import url(https://fonts.googleapis.com/css?family=Fredoka+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Ubuntu:700&display=swap);
.header{display:flex;flex-direction:column;align-items:center}.header__logo{width:300px}

.preview{width:180px;height:270px;margin:0 15px 0 15px;display:flex;flex-direction:column;align-items:center}.preview__photo{height:180px;width:180px;border-radius:50%;background-color:rgba(159,144,175,0.623);margin-bottom:10px}.preview__info{display:flex;align-items:center;flex-direction:column;background-color:rgba(159,144,175,0.623);border-radius:3px;width:180px;padding:2px}.preview__info--gem{position:absolute;-webkit-transform:translateX(50px) translateY(-60px);transform:translateX(50px) translateY(-60px);height:60px}.preview__info--name{font-family:'Ubuntu', sans-serif;text-align:center}

.main{display:flex;align-items:center;justify-content:center;flex-wrap:wrap}.notfound{display:flex;align-items:center;flex-direction:column}.notfound__img{width:300px}

.details{margin:35px;display:flex;flex-direction:column;justify-content:center;align-items:center;border-radius:20px;background-color:rgba(159,144,175,0.623)}@media all and (min-width: 768px){.details{flex-direction:row}}.details__origin{display:flex;align-items:center;justify-content:center;width:100%}.details__origin--img{width:30px;margin:10px}.details__origin--name{margin:10px}.details__img{display:flex;flex-direction:column;justify-content:center;align-items:center;width:200px}@media all and (min-width: 768px){.details__img{width:300px}}.details__img--name{margin-top:10px;font-size:smaller;font-family:'Roboto', sans-serif;text-transform:uppercase;text-align:center;color:#3f1649}.details__gem{display:flex;flex-direction:column;align-items:center}.details__gem--slider{-webkit-transform:translateY(-60px);transform:translateY(-60px);width:320px}.details__gem--img{color:blue}.details__gem--name{font-family:'Staatliches', cursive}.details__fusion{display:flex;flex-direction:column;align-items:center}.details__fusion--slider{width:100px;height:100px}.details__fusion--img{height:100px}.details__info{margin-top:10px;display:grid;width:100%;grid-template-columns:repeat(2, 1fr);grid-column-gap:0px;grid-row-gap:10px}.details__info--name{font-family:'Fredoka One', cursive;font-size:25px;text-align:center}.details__info--nicknames{text-transform:uppercase;font-size:11px;font-family:'Roboto', sans-serif;color:#333;margin-bottom:10px}.details__weapons{display:flex}.details__weapons--img{height:200px}

.filter{display:flex;flex-direction:column;justify-content:center;align-items:center;margin:0}.filter__label{font-family:'Ubuntu', sans-serif}.filter__input{border-radius:2px;font-size:15px;padding:2px;border-style:none;font-family:'Ubuntu', sans-serif}.Collapsible{margin:40px;font-family:'Ubuntu', sans-serif}.Collapsible__trigger{color:hotpink;cursor:pointer;background:white}.collapsible__container{display:flex;flex-direction:column;justify-content:center;align-items:center}

*{box-sizing:border-box;margin:0;padding:0;outline:none}body{background:url("https://66.media.tumblr.com/a639bda231b0f3a803643073e4df0d63/tumblr_oy1ql1bPTh1u21nzho1_1280.png");background-size:cover;background-attachment:fixed;background-repeat:no-repeat;background-position:center}

